:root {
    --main-color: #F58ED6;
}

.header {
    position: -webkit-sticky;
    position: sticky;
    background-size: 100%;
    background-color: #F58ED6;
    background-color: var(--main-color);
    top: 0;
    width: 100%;
    height: 46px;
    overflow: hidden;
    z-index: 100;
}

.header .responsiveMenu{
    visibility: hidden;
}
/*logo with white background*/

.header .whiteDiv {
    position: absolute;
    z-index: 115;
    display: inline-block;
    background-color: rgb(255, 255, 255);
    -webkit-clip-path: ellipse(100% 125% at 0% 100%);
            clip-path: ellipse(100% 125% at 0% 100%);
    height: 100%;
    width: 50%;
}

#logoHeader {
    z-index: 120;
    position: absolute;
    left: 0px;
    width: 90px;
    height: 45px;
    margin-left: 50px;
}

.nav-container {
    position: absolute;
    right: 0;
    z-index: 115;
    display: block;
    height: 100%;
}

.nav-container a {
    width: 125px;
    height: 100%;
    font-family: 'Roboto', sans-serif, cursive;
    font-size: 17px;
    font-weight: 500;
    float: right;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0px;
    margin-top: 3px;
    margin-bottom: 3px;
    text-decoration: none;
    border-left: solid 1px #000;
    color: #000;
}

.header .nav-container a:nth-child(2) {
    border-right: solid 1px #000;
    margin-right: 50px;
}

.header .nav-container a:hover {
    background: linear-gradient(0deg, rgba(255, 255, 255, 0.5) 0%, rgba(255, 255, 255, 0) 100%);
}

/*Button*/

.nav-container .barMenu-btn-Appointment {
    border-left: none;
    background-color: #fff;
    border-radius: 10px;
    border: 1px solid #F58ED6;
    border: 1px solid var(--main-color);
    margin-right: 15px;
    margin-left: 15px;
    color: #F58ED6;
    color: var(--main-color);
    height: 40px;
}

.nav-container .barMenu-btn-Appointment:hover {
    color: #fff;
    border: 1px solid #fff;
    background-color: #F58ED6;
    background-color: var(--main-color);
}

@media screen and (max-width: 700px) {
    .header .nav-container {
        visibility: hidden;
    }
    .header #logoHeader {
        margin-left: 15px;
    }
    .header .responsiveMenu .buttons {
        position: absolute;
        right: 0;
        top: 0;
        height: 100%;
        z-index: 115;
        margin-right: 15px;
        height: -webkit-min-content;
        height: min-content;
        display: grid;
        grid-template-columns: -webkit-min-content 25%;
        grid-template-columns: min-content 25%;
        grid-template-rows: 45px;
        justify-content: center;
        align-content: center;
        justify-items: center;
        align-items: center;
        overflow: hidden;
    }
    .responsiveMenu .btnResponsiveMenu {
        width: 50px;
        height: 70%;
        z-index: 110;
        border: none;
        background: no-repeat center;
        background-size: contain;
        cursor: pointer;
    }
    /*Button */
    .responsiveMenu .barMenu-btn-Appointment {
        border-left: none;
        background-color: #fff;
        border-radius: 10px;
        border: 1px solid #F58ED6;
        border: 1px solid var(--main-color);
        color: #F58ED6;
        color: var(--main-color);
        margin: 0 5px 0 0;
        height: 40px;
    }
    .responsiveMenu .barMenu-btn-Appointment:hover {
        color: #fff;
        border: 1px solid #fff;
        background-color: #F58ED6;
        background-color: var(--main-color);
    }
    .header {
        overflow: visible;
    }
    /*Menu Options*/
    .responsiveMenu .nav-container-responsive {
        visibility: hidden;
        position: absolute;
        z-index: 120;
        right: 0%;
        top: 45px;
        width: 100%;
        height: -webkit-min-content;
        height: min-content;
        overflow: hidden;
        border-radius: 0 0 20px 20px;
    }
    .responsiveMenu .nav-container-responsive ul {
        background: #F58ED6;
        background: var(--main-color);
        position: relative;
        list-style: none;
        padding: 0;
        width: 100%;
        height: auto;
        margin: 0;
        margin-left: 2.5%;
        right: -100%;
        -webkit-animation-iteration-count: 1;
                animation-iteration-count: 1;
        -webkit-animation-duration: 0.2s;
                animation-duration: 0.2s;
        -webkit-animation-fill-mode: forwards;
                animation-fill-mode: forwards;
    }
    .responsiveMenu .nav-container-responsive ul li {
        border-bottom: 1px solid #fff;
    }
    .responsiveMenu .nav-container-responsive a {
        display: block;
        font-size: 24px;
        text-decoration: none;
        font-family: 'Roboto', sans-serif, cursive;
        text-align: center;
        padding: 10px;
        color: #fff;
    }
    .responsiveMenu .nav-container-responsive a:hover {
        background: linear-gradient(0deg, rgba(255, 255, 255, 0.5) 0%, rgba(255, 255, 255, 0) 100%);
    }
    .responsiveMenu .nav-container-responsive a:last-child {
        border-bottom: none;
    }
    /*Animation to show and hidde options*/
    @-webkit-keyframes showMenu {
        from {
            right: -100%;
        }
        to {
            right: 2.5%;
        }
    }
    @keyframes showMenu {
        from {
            right: -100%;
        }
        to {
            right: 2.5%;
        }
    }
    @-webkit-keyframes hiddeMenu {
        from {
            right: 2.5%;
        }
        to {
            right: -100%;
        }
    }
    @keyframes hiddeMenu {
        from {
            right: 2.5%;
        }
        to {
            right: -100%;
        }
    }
}
.slider-container {
    margin: 0px 50px 100px 50px;
    width: auto;
    height: content;
    max-height: 600px;
    overflow: hidden;
    position: relative;


    border-radius: 0 0 25px 25px;
}

/*Arrows*/

.slider-container .rightArrow, .slider-container .leftArrow {
    width: 0;
    height: 0;
    border-style: solid;
    background: none;
    position: absolute;
    z-index: 10;

    bottom: 50%;
    margin-left: 15px;
    margin-right: 15px;

    cursor: pointer;
}

.slider-container .rightArrow{
    border-color: transparent transparent transparent #7e7e7e;
    border-width: 35px 0 35px 25px;
    right: 0;
}
.slider-container .leftArrow{
    border-color: transparent #7e7e7e transparent transparent;
    border-width: 35px 25px 35px 0;

}

/*images*/
.slider-content {
    width: 100%;
    height: 100%;
    white-space: nowrap;
    transition-duration: 0.4s;
}

.slider-content img {
    width: 100%;
    height: 100%;
    display: inline-block;
    object-fit: fill;
}

@media screen and (max-width: 700px) {
    
    .slider-container{
        margin: 15px;
        margin-top: 0;
        min-height: 250px;
    }
    
    /*Arrows*/
    .slider-container .rightArrow, .slider-container .leftArrow {
        margin-left: 5px;
        margin-right:5px;
        bottom: 50%;
    }
    
    .slider-container .rightArrow{
        border-color: transparent transparent transparent #7e7e7e;
        border-width: 25px 0 25px 25px;
        right: 0;
    }
    .slider-container .leftArrow{
        border-color: transparent #7e7e7e transparent transparent;
        border-width: 25px 25px 25px 0;
        
    }
    /*images*/
    .slider-content img {
        min-height: 250px;
    }
}


.card-service {
    background-color: #fff;
    overflow: visible;
    position: relative;
    width: 100%;
    height: 525px;
}

.card-side {
    width: 320px;
    height: 525px;
    
    overflow: hidden;
    border: 1px solid var(--main-color);
    border-radius: 30px;
    box-shadow: 5px 5px 10px 5px rgba(0, 0, 0, 0.25);
    
    transition: all 0.4s ease;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    position: absolute;
    top:0;
    left: 0;

    text-align: center;
}

/*Front-card*/

.front-card .topPart {
    position: relative;
    width: 100%;
    height: 50%;
    overflow: hidden;
}

.front-card .topPart h2 {
    margin: 0;
    display: block;
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgba(0, 0, 0, 0.65);
    color: #fff;
    height: 45px;
    font-size: 30px;
    font-family: 'Dancing Script', cursive;
    text-transform: uppercase;
}

.front-card .topPart img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.front-card .bottomPart {
    position: relative;
    height: 50%;
}

.front-card p {
    font-size: 20px;
    font-family: 'Crimson Text', serif;
    margin: 20px 20px 0px;
    height: 65%;
}

.front-card button {
    background: none;
    border: none;
    border: solid 1px var(--main-color);
    border-radius: 10px;
    height: 50px;
    width: 150px;
    margin: 5px;
    font-size: 20px;
    font-family: 'Crimson Text', serif;
    color: var(--main-color);
    cursor: pointer;
}

.front-card button:hover {
    background-color: #f3a8dd58;
}

/*Back- card*/

.back-card {
    -webkit-transform: rotateY(-180deg);
            transform: rotateY(-180deg);
